.dropbtn {
    background-color: white;
    color: black;
    padding: 16px;
    font-size: 16px;
    font-family: "Playfair Display", serif;
}

.left{
    float: left;
    position:relative;
}
.right{
    float: right;
    position: relative;
}

.dropdown-content-left {
    display: none;
    position: absolute;
    background-color: black;
    font-family: "Playfair Display", serif;
    min-width: 160px;
    -webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
            box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

.dropdown-content-left a {
    color: white;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

.dropdown-content-right {
    display: none;
    position: absolute;
    background-color: black;
    font-family: "Playfair Display", serif;
    min-width: 160px;
    -webkit-box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
            box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

.dropdown-content-right a {
    color: white;
    padding: 16px 200px 12px 0px; 
    text-decoration: none;
    display: block;
    }

.dropdown-content-left a:hover {background-color: white;
color:black;}

.dropdown:hover .dropdown-content-left {display: block;}

.left:hover .dropbtn {background-color: #adb8ae;}

.dropdown-content-right a:hover {background-color: white;
color:black;}

.dropdown:hover .dropdown-content-right {display: block;}

.right:hover .dropbtn {background-color: #adb8ae;}



